<template>
	<div id="activity-bargainGoods">
		<public-header title="创建商品活动"></public-header>
		<div class="content">
			<div class="fix-search van-hairline--bottom">
				<router-link tag="div" :to="util.getUrl({path: '/pages/activity/bargainGoodsSearch'})" class="search-input" >搜索商品名称</router-link>
			</div>
			<van-row class="content-inner">
				<van-col span="5" class="wh-100 tag-list">
					<div class="tag-item van-hairline--bottom" :class="{active: cIndexActive == index}" v-for="(item, index) in categorys" @click="onChangeCategory(index)" :key="item.id">
						<div class="tag-inner">{{item.title}}</div>
						<div class="badge" v-if="item.total > 0">{{item.total}}</div>
					</div>
				</van-col>
				<van-col span="19" class="wh-100 goods-wrap">
					<div class="goods-top van-hairline--bottom">
						<div class="category-title">{{cTitleActive}}</div>
					</div>
					<div class="padding-15-lr bg-default hide">
						<div class="rank van-hairline--bottom">
							<div class="rank-item" :class="{active: orderby == 'sailed desc'}">销量最高</div>
							<div class="rank-item">好评优先</div>
							<div class="rank-item flex" :class="{active: orderby == 'price desc'}">
								价格
								<div class="triangle">
									<div class="triangle-top selected"></div>
									<div class="triangle-bottom "></div>
								</div>
							</div>
						</div>
					</div>
					<div class="goods-list padding-15-lr bg-default" >
						<van-cell-group class="border-0px" v-if="goodsList.length > 0">
							<div class="goods-item van-hairline--bottom" v-for="(item,index) in goodsList" :key="item.id">
								<div @click="onToggleCheckBox(index, item.cid)">
									<van-row >
										<van-col span="6">
											<img class="goods-thumb" :src="item.thumb" alt="">
										</van-col>
										<van-col span="15">
											<div class="w-100 ellipsis font-bold">{{item.title}}{{item.id}}</div>
											<div class="margin-5-t c-gray font-12">
												<span class="padding-10-r">月售{{item.sailed}}</span>
												<span>好评{{item.per_comment_good}}%</span>
											</div>
											<div class="discount flex margin-5-t hide" v-if="item.is_bargain">
												<span class="mark flex ">
													<span>¥{{item.discount_price}}</span>
													<span>折扣</span>
												</span>
												<span class="c-gray font-12 margin-5-l">351天后到期</span>
											</div>
											<div class="margin-5-t">
												<span>¥{{item.price}}</span>
												<span class="padding-10-l c-gray font-12 hide" >已参与其他活动</span>
											</div>
										</van-col>
										<van-col span="3" class="text-right">
											<div class="radio-box icon" :class="{'icon-check': item.is_bargain}"></div>
										</van-col>
									</van-row>
								</div>
								<div class="goods-set" v-if="item.is_bargain">
									<div class="flex-lr font-12 margin-5-b">
										<span>活动价</span>
										<div class="input-unit" >
											<input type="text" v-model.number="item.discount_price">
											<span class="font-12 bg-default">元</span>
										</div>
									</div>
									<div class="flex-lr font-12 margin-5-b">
										<span>每单限购</span>
										<div class="input-unit" >
											<input type="text" v-model.number="item.max_buy_limit">
											<span class="font-12 bg-default">元</span>
										</div>
									</div>
									<div class="flex-lr font-12 margin-5-b">
										<span>每日库存</span>
										<div class="input-unit" >
											<input type="text" v-model.number="item.discount_total">
											<span class="font-12 bg-default">份</span>
										</div>
									</div>
									<div class="flex-lr font-12 margin-5-b">
										<span>当日剩余库存</span>
										<div class="input-unit" >
											<input type="text" v-model.number="item.discount_available_total">
											<span class="font-12 bg-default">份</span>
										</div>
									</div>
									<div class="flex-lr font-12">
										<span>新用户专享</span>
										<iswitch :value="item.poi_user_type" text-open="ON" text-close="OFF" condition-open="new" condition-close="all" @change="jsToggleSwitch" :keys="`goodsList.${index}.poi_user_type`"></iswitch>
									</div>
								</div>
							</div>
						</van-cell-group>
						<div class="no-data" v-else>
							<img src="static/img/order_no_con.png" alt="">
							<p>该分类下暂无商品</p>
						</div>
					</div>
				</van-col>
			</van-row>
			<div class="fix-bottom">
				<van-button size="normal" block class="bg-info" @click="onSubmit">选好了, 立即创建</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData} from "@/controller/funcCommon"
	import {mapState, mapMutations} from 'vuex'
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				showPreLoading: true,
				cid: 0,
				bargain_id: 0,
				orderby: 'sailed desc',
				categorys: [],
				cIndexActive: 0,
				cTitleActive: '',
				goodsAll: [],
				goodsList: [],
				cids: [],
				bargainGoodsOld: []
			}
		},
		computed: {
			...mapState([
				'bargainGoods'
			]),
		},
		methods: {
			...mapMutations([
				'setBargainGoods'
			]),
			onGetData(isInit) {
				var that = this;
				getData({
					vue: that,
					url: 'manage/activity/bargain/goods_list',
					data: {
						cid: that.cid,
						bargain_id: that.bargain_id
					},
					success: (res) => {
						let bargainGoods = {};
						if(res.bargain_goods.length > 0) {
							for(let z in res.bargain_goods) {
								bargainGoods[res.bargain_goods[z]['id']] = res.bargain_goods[z];
							}
						}
						that.bargainGoodsOld = bargainGoods;
						if(that.util.length(that.bargainGoods) > 0) {
							bargainGoods = that.bargainGoods;
						} else {
							that.setBargainGoods(bargainGoods);
						}
						if(isInit) {
							let categorys = res.categorys;
							that.cids = res.cids;
							if(that.util.length(bargainGoods) > 0) {
								let cIndex = '';
								for(let y in bargainGoods) {
									cIndex = that.cidToCindex(bargainGoods[y].cid);
									categorys[cIndex].total += 1;
								}
							}
							that.categorys = [...categorys];
						}

						let goods = res.goods;
						let temp = {}
						if(that.util.length(bargainGoods) > 0) {
							for(let x in goods) {
								temp = goods[x];
								if(bargainGoods.hasOwnProperty(temp['id'])) {
									goods[x] = bargainGoods[temp['id']];
								} else {
									continue;
								}
							}
						}
						that.goodsAll[that.cIndexActive] = [...goods];
						that.cTitleActive = that.categorys[that.cIndexActive].title;
						that.goodsList = [...that.goodsAll[that.cIndexActive]];
					}
				})
			},
			onChangeCategory(index) {
				var that = this;
				//点击切换之前先把goodsList的数据更新到goodsAll
				that.goodsAll[that.cIndexActive] = that.goodsList;

				that.cIndexActive = index;
				if(that.goodsAll.hasOwnProperty(index)) {
					that.cTitleActive = that.categorys[that.cIndexActive].title;
					that.goodsList = [...that.goodsAll[that.cIndexActive]];
				} else {
					that.cid = that.categorys[that.cIndexActive].id;
					that.onGetData();
				}
			},
			onToggleCheckBox(index, cid) {
				var that = this;
				let num = 1;
				if(that.goodsList[index].is_bargain) {
					num = -1;
				}
				let cIndex = that .cIndexActive;
				that.categorys[cIndex].total = that.categorys[cIndex].total + num;
				that.goodsList[index].is_bargain = !that.goodsList[index].is_bargain;
				that.goodsAll[cIndex] = that.goodsList;
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			cidToCindex(cid) {
				var that = this;
				cid = parseInt(cid);
				return that.cids.indexOf(cid);
			},
			onSubmit() {
				var that = this;
				var bargainGoods = [];
				if(that.util.length(that.bargainGoods) > 0) {
					bargainGoods = that.bargainGoods;
				}
				var itemTamp = [];
				for(var x in that.goodsAll) {
					itemTamp = that.goodsAll[x];
					if(itemTamp.length > 0) {
						for(var y in itemTamp) {
							if(!itemTamp[y].is_bargain) {
								if(bargainGoods.hasOwnProperty(itemTamp[y].id)) {
									delete bargainGoods[itemTamp[y].id];
								}
								continue;
							}
							bargainGoods[itemTamp[y].id] = itemTamp[y];
						}
					}
				}
				if(that.util.length(bargainGoods) > 0) {
					that.setBargainGoods(bargainGoods);
					that.$router.go(-1);
				} else {
					that.util.$toast('请选择参与活动的商品');
				}
			}

		},
		mounted() {
			var that = this;
			let query = that.util.parseQuery(that.$route.query);
			if(query.bargain_id > 0) {
				that.bargain_id = query.bargain_id;
			}
			this.onGetData(true);
		}
	}
</script>

<style>
	#activity-bargainGoods .fix-search{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
	#activity-bargainGoods .fix-search .search-input{
		width: 100%;
		padding: 10px 0;
		border-radius: 5px;
		background-color: #f5f5f5;
		color: #7a7a7a;
		font-size: 14px;
		text-align: center;
	}
	#activity-bargainGoods .fix-bottom{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
	#activity-bargainGoods .content-inner{
		position: absolute;
		top: 54px;
		bottom: 65px;
		left: 0;
		right: 0;
	}
	#activity-bargainGoods .content-inner .tag-list{
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#activity-bargainGoods .tag-list .tag-item{
		padding:10px;
		color:#7a7a7a;
		font-size: 14px;
		position: relative;
	}
	#activity-bargainGoods .tag-list .tag-item .tag-inner{
		text-align:center;
		overflow:hidden;
		display:-webkit-box;
		width:100%;
		line-height:17px;
		box-sizing:border-box;
		text-overflow:ellipsis;
		-webkit-box-orient:vertical;
		-webkit-line-clamp:2;
	}
	#activity-bargainGoods .tag-list .tag-item.active{
		color:#333;
		background-color:#fff;
	}
	#activity-bargainGoods .tag-list .tag-item.active:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 3px;
		background-color: #33aafc;
	}
	#activity-bargainGoods .tag-list .tag-item .badge{
		position: absolute;
		top: 5px;
		right: 5px;
		z-index: 10;
		background-color: #ff2c4c;
		color: #fff;
		border-radius: 50px;
		font-size: 12px;
		padding: 1px 3px;
		min-width: 14px;
		text-align: center;
	}
	#activity-bargainGoods  .goods-wrap{
		position: relative;
	}
	#activity-bargainGoods .goods-list{
		position: absolute;
		top: 34px;
		right: 0;
		bottom: 0;
		left: 0;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#activity-bargainGoods .category-title{
		padding: 10px 15px;
		background-color: #fff;
		font-size: 14px;
	}
	#activity-bargainGoods .rank{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding: 10px 0;
	}
	#activity-bargainGoods .rank .rank-item{
		display: flex;
		justify-content: center;
		color: #7a7a7a;
	}
	#activity-bargainGoods .rank .rank-item.active{
		color: #33aafc;
	}
	#activity-bargainGoods .rank .triangle{
		height:100%;
		padding-top:3px;
		padding-left:3px;
	}
	#activity-bargainGoods .rank .triangle .triangle-top{
		width: 0;
		height: 0;
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-bottom: 5px solid #ccc;
		margin-bottom:2px;
	}
	#activity-bargainGoods .rank .triangle .triangle-top.selected{
		border-bottom: 5px solid #000;
	}
	#activity-bargainGoods .rank .triangle .triangle-bottom{
		width: 0;
		height: 0;
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-top: 5px solid #ccc;
	}
	#activity-bargainGoods .rank .triangle .triangle-bottom.selected{
		border-top: 5px solid #000;
	}
	#activity-bargainGoods .goods-item{
		padding: 15px 0;
		background-color: #fff;
		font-size: 14px;
	}
	#activity-bargainGoods .goods-item .goods-thumb{
		width: 50px;
		height: 50px;
		border-radius: 5px;
		overflow: hidden;
	}
	#activity-bargainGoods .goods-item .van-radio{
		margin-top: 20px;
	}
	#activity-bargainGoods .goods-item .discount .mark{
		border-radius: 3px;
		background-color: #ff2c4c;
		font-size: 11px;
		border: 1px solid #ff2c4c;
		overflow: hidden;
	}
	#activity-bargainGoods .goods-item .discount .mark span:first-child{
		padding: 2px 5px;
		color: #fff;
	}
	#activity-bargainGoods .goods-item .discount .mark span:last-child{
		padding: 2px 5px;
		color: #ff2c4c;
		background-color: #fff;
	}
	#activity-bargainGoods .goods-item .radio-box{
		margin-top: 20px;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #aaa;
		color: #fff;
	}
	#activity-bargainGoods .goods-item .radio-box.icon-check{
		background-color: #33aafc;
		border-color: #33aafc;
	}
	#activity-bargainGoods .goods-item .goods-set{
		width: 70%;
		padding: 10px;
		background-color: #f5f5f5;
		margin: 10px 10% 0 20%;
	}
	#activity-bargainGoods .goods-item .iswitch{
		height: 22px;
		min-width: 73px;
	}
	#activity-bargainGoods .no-data{
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		padding: 0;
		min-height: 0;
		transform: translateY(-50%);
		width: 100%;
	}
</style>


